/* Product Details Section */
.product-details {
  margin-top: 20vh; /* Sufficient margin-top for all screen sizes */
  overflow-y: auto; /* Adds scroll if content overflows vertically */
  max-height: 80vh; /* Limits the height to 80% of the viewport height */
}

/* Product Details Info */
.product-details-info {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 20px; /* Add padding if needed */
}

/* Styling for h6 elements inside Product Details Info */
.product-details-info h6 {
  margin-bottom: 20px !important;
}

/* Similar Products Section */
.similar-products {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
  overflow-y: hidden; /* Removes scroll bar by default */
}

.similar-products .card {
  background-color: rgba(128, 128, 128, 0.097);
  width: 20rem;
  height: auto;
  margin-bottom: 20px; /* Adds margin between cards */
}

/* Card Name and Price */
.similar-products .card-name-price {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

/* Card Price Styling */
.similar-products .card-price {
  color: green;
  font-weight: bold;
}

/* Button Styling inside Similar Products */
.similar-products button {
  border-radius: 0;
  width: 100%;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  font-size: 14px;
  font-weight: bold;
}

/* Card Text Styling */
.similar-products .card-text {
  color: rgb(90, 89, 89);
}

/* Card Image Hover Effect */
.similar-products .card img:hover {
  transform: scale(0.9);
}

/* Card Image Top Styling */
.similar-products .card-img-top {
  height: 300px !important;
  object-fit: cover; /* Ensures image covers the area without distortion */
}

/* Media Queries for Small Screens */
@media (max-width: 900px) {
  /* Adjust Product Details Section */
  .product-details {
    margin-top: 20vh;
    max-height: 70vh;
  }

  /* Product Details Info */
  .product-details-info {
    padding: 6px;
  }

  /* Similar Products Section */
  .similar-products {
    justify-content: space-between;
    align-items: center;
  }

  /* Card Styling Adjustments */
  .similar-products .card {
    width: 12rem;
    margin-bottom: 15px;
  }

  /* Adjust Button Font Size */
  .similar-products button {
    font-size: 12px;
  }

  /* Card Image Top Styling */
  .similar-products .card-img-top {
    height: 300px !important;
    object-fit: cover; /* Ensures image covers the area without distortion */
    display: block;
    margin: auto; /* Centers the image horizontally */
    position: relative; /* Centers the image vertically */
  }

  /* Adjust Card Text */
  .similar-products .card-text {
    font-size: 14px;
  }
}

@media (max-width: 480px) {
  /* Further Adjustments for Very Small Screens */
  .product-details {
    margin-top: 20vh;
    max-height: 60vh;
  }

  .similar-products {
    justify-content: center;
  }

  .similar-products .card {
    width: 100%;
    margin-bottom: 10px;
  }

  .similar-products .card-img-top {
    height: 200px !important;
  }

  .similar-products button {
    font-size: 10px;
  }

  .similar-products .card-text {
    font-size: 12px;
  }
}
