.intro {
  margin-top: 10vh;
  background-color: rgb(32, 31, 31);
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
}

.intro-img {
  height: 700px;
  object-fit: cover;
}

.intropara {
  width: 50%;
  padding: 20px;
  font-size: 25px;
  line-height: 1.8;
  color: white;
  text-align: justify;
  height: 100%;
  display: flex;
  align-items: center;
  animation: fadeInSlideIn 2s ease-in-out;
}

@keyframes fadeInSlideIn {
  0% {
    opacity: 0;
    transform: translateX(-50px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

/* Small screens (mobile) */
@media (max-width: 1340px) {
  .intro {
    flex-direction: column; /* Stack the image and paragraph vertically */
    align-items: center; /* Center-align the content */
  }

  .intro-img {
    width: 100%; /* Make the image take full width */
    height: auto; /* Maintain aspect ratio for smaller screens */
  }

  .intropara {
    width: 100%; /* Match the width of the image */
    font-size: 18px; /* Adjust font size for readability */
    text-align: center; /* Center-align text for better aesthetics */
    padding: 10px; /* Uniform padding for better layout */
  }
}

.banner-img {
  margin-top: 60px;
}

.home-page h1 {
  color: gray;
  font-family: "Playfair Display", serif;
}

.home-page {
  display: flex;
  flex-wrap: wrap;
}
.home-page .card {
  background-color: rgba(128, 128, 128, 0.097);
  width: 18rem;
}

.home-page .card-name-price {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.home-page .card-price {
  color: green;
  font-weight: bold;
}

.home-page button {
  border-radius: 0;
  width: 100%;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  font-size: 14px;
  font-weight: bold;
}
.btn-info{
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  border: #333;
  background-color: black;
  color: aliceblue;
  position: absolute; 
  bottom: 0;
  left: -0.25rem;
}

.btn-info:hover{
  background-color: gold;
  color: black;
}

.home-page .card-text {
  color: rgb(90, 89, 89);
}

.home-page .card img:hover {
  transform: scale(0.9);
}

.home-page .card-img-top {
  height: 300px !important;
}

/* Media query for small screens */
@media (max-width: 900px) {
  .home-page .card {
    width: 45%; /* Reduce card width to fit two cards per row */
    margin: 1%; /* Add some margin for spacing */
  }

  .home-page .card-img-top {
    height: auto !important; /* Maintain aspect ratio */
  }

  .home-page button {
    font-size: 12px; /* Slightly smaller font for buttons */
  }

  .home-page .card-name-price {
    flex-direction: row; /* Keep name and price side by side */
    justify-content: space-between;
  }

  .home-page .container {
    display: flex; /* Ensure container uses flex layout */
    flex-wrap: wrap; /* Wrap cards to the next row if necessary */
    justify-content: space-between; /* Add space between cards */
  }
}

@media (max-width: 500px) {
  .home-page .card {
    width: 90%; /* Full width for a single card with some margin */
    margin: 5% auto; /* Center the card and add spacing */
  }

  .home-page .card-name-price {
    flex-direction: column; /* Stack name and price vertically */
    align-items: flex-start; /* Align to the left */
  }

  .home-page button {
    font-size: 10px; /* Smaller font size for buttons */
  }

  .home-page .container {
    display: flex;
    flex-wrap: nowrap; /* No wrapping needed for a single card per row */
    flex-direction: column; /* Stack cards vertically */
    justify-content: center; /* Center cards vertically */
    align-items: center; /* Center cards horizontally */
  }
}


/* ==================
filters
============== */
.filters h4 {
  margin-top: 100px;
  color: gray !important;
  border-bottom: 1px solid rgba(128, 128, 128, 0.337);
}

.ant-checkbox-wrapper:first-of-type {
  margin-left: 8px;
}

.ant-checkbox-wrapper {
  margin: 2px;
}
.ant-radio-wrapper {
  margin-bottom: 5px !important;
  margin-left: 10px;
}

.filters button {
  background-color: black;
  width: 100%;
  border: none;
  border-radius: 0;
  margin-top: 20px;
}

.loadmore {
  color: green;
  font-weight: bold;
  font-size: 20px !important;
}
.whatsapp-icon {
  position: fixed;
  bottom: 20px;
  right: 20px;
  width: 60px;
  height: 60px;
  z-index: 1000;
}

.whatsapp-icon img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.whatsapp-icon img:hover {
  transform: scale(1.1);
}

.testimonials-container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 50px 20px;
  text-align: center;
}

.testimonials-title {
  font-size: 36px;
  font-weight: bold;
  color: #333;
  margin-bottom: 40px;
}

.slider-container {
  width: 100%;
}

.slider-container .slider {
  width: 100%;
  max-width: 900px;
  background-color: #f8f8f8;
  margin: 0 auto;
  border-radius: 20px;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
}

@media (max-width: 768px) {
  .testimonials-title {
    font-size: 28px; /* Reduced font size for better fit on smaller screens */
  }

  .slider-container .slider {
    max-width: 100%; /* Allow slider to take full width */
    border-radius: 15px; /* Slightly smaller border radius for consistency */
  }
}

@media (max-width: 480px) {
  .testimonials-title {
    font-size: 24px; /* Further reduced font size for extra small screens */
  }

  .slider-container .slider {
    border-radius: 10px; /* Even smaller radius for very small screens */
  }
}
